import styled from "styled-components";

const Project = (props) => {
  return (
    <>
      <ProjectBackground
        key={props.name}
        style={{
          backgroundColor: "#25064d",
          backgroundImage: `url(${props.thumbnail})`,
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
        }}
      >
        <ProjectForeground style={{ backgroundColor: "rgba(0, 0, 0, 0.6)" }}>
          {props.name}
          <div style={{ fontSize: ".6em", lineHeight: "2em" }}>
            {props.technologies}
          </div>
          <Icons>
            <LinkColour href={props.demo}>
              <i className="fas fa-link"></i>
            </LinkColour>
            <LinkColour href={props.link}>
              <i className="fab fa-github fa-lg"></i>
            </LinkColour>
          </Icons>
        </ProjectForeground>
      </ProjectBackground>
    </>
  );
};

const ProjectBackground = styled.div`
  color: #b382dd;
  min-width: 330px;
  min-height: 300px;
  margin: 10px 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.25em;
`;

const ProjectForeground = styled.div`
  height: 290px;
  width: 320px;
  padding: 10px 10px 0px 10px;
`;

const Icons = styled.div`
  display: flex;
  flex-direction: row;
  position: relative;
  bottom: -225px;
  padding-left: 275px;
`;

const LinkColour = styled.a`
  display: block;
  color: #b382dd;
  padding: 3px;
`;

export default Project;
